import React from "react"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

/*
 * This component is built using `gatsby-image` to automatically serve optimized
 * images with lazy loading and reduced file sizes. The image is loaded using a
 * `useStaticQuery`, which allows us to load the image from directly within this
 * component, rather than having to pass the image data down from pages.
 *
 * For more information, see the docs:
 * - `gatsby-image`: https://gatsby.dev/gatsby-image
 * - `useStaticQuery`: https://www.gatsbyjs.org/docs/use-static-query/
 */

// // https://stackoverflow.com/questions/55122752/reusable-gatsby-image-component-with-dynamic-image-sources
const Image = ({ name = "placeholderImage", imgStyle, style }) => {
  console.log("Name in image: ", name)

  const data = useStaticQuery(graphql`
    query {
      placeholderImage: file(relativePath: { eq: "soap_foam_small.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderImageVar1: file(relativePath: { eq: "soap_small.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderImageVar2: file(relativePath: { eq: "soap_small2.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderImageVar1Hd: file(relativePath: { eq: "yellow_soap_hd.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderImageVar2Hd: file(relativePath: { eq: "green_soap_hd.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderImageVar3Hd: file(relativePath: { eq: "black_soap_hd.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }

      placeholderLogo: file(relativePath: { eq: "logo_sample.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderPerson: file(relativePath: { eq: "person_placeholder.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderPerson2: file(
        relativePath: { eq: "person_placeholder2.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      placeholderPerson3: file(
        relativePath: { eq: "person_placeholder3.png" }
      ) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
      exampleImage: file(relativePath: { eq: "gatsby-astronaut.png" }) {
        childImageSharp {
          fluid(maxWidth: 300) {
            ...GatsbyImageSharpFluid
          }
        }
      }
    }
  `)

  if (!data[name]) {
    console.log("NAME", name)
  }

  return (
    <Img
      fluid={data[name].childImageSharp.fluid}
      imgStyle={imgStyle}
      style={style}
    />
  )
}

export default Image
