import { useState, useRef, useEffect, useLayoutEffect } from "react"

const getScrollPositionY = () => {
  if (typeof window === `undefined`) return 0

  return window.pageYOffset !== undefined
    ? window.pageYOffset
    : document.body.scrollTop
}

const useScrollPositionY = (throttleDelay = 100) => {
  let throttleTimeout = null
  const [scrollPositionY, setScrollPositionY] = useState(null)

  const updatePosition = () => {
    const posY = getScrollPositionY()
    setScrollPositionY(posY)
    throttleTimeout = null
  }

  const handleScroll = () => {
    if (throttleTimeout === null) {
      throttleTimeout = setTimeout(updatePosition, throttleDelay)
    }
  }
  useEffect(() => {
    window.addEventListener("scroll", handleScroll, true)

    return window.removeEventListener("scroll", handleScroll)
  }, [])

  return scrollPositionY
}

export default useScrollPositionY
