import React from "react"
import Image from "../components/image"

import styled from "styled-components"

const CONTAINER_HEIGHT = 60

const Container = styled.div`
  height: ${CONTAINER_HEIGHT}px;
`

const NavigationContainer = styled.div`
  display: flex;
  padding: 0 2em;
  height: ${({ shrink }) => (shrink ? 50 : CONTAINER_HEIGHT)}px;
  transition: height 1s, scale 1s, translate 1s;
  background: orange;
  // opacity: 0.5;
  // position: sticky;
  // top: 0;
  z-index: 1000;

  ${({ shrink }) =>
    shrink &&
    `
      .NavigationLogo {
        transform: scale(0.5) translate3d(0, -50%, 0);

      }
    `}
`
const NavigationLogo = styled.div`
  height: 100px;
  width: 100px;
  min-width: 100px;
  border-radius: 100px 100px 100px 100px;
  overflow: hidden;
  transition: transform 1s;
  // .gatsby-image-wrapper {
  //   position: absolute;
  // }
`
const NavigationList = styled.ul`
  list-style: none;
  width: 100%;
  height: 100%;
  // overflow: hidden;
  // height: ${({ shrink }) => (shrink ? 50 : 100)}%;
  transition: height 1s, scale 1s, translate 1s;

  margin: 0;

  display: flex;
`

const NavigationListItem = styled.li`
  font-style: italic;

  border: 1px solid gray;
  background: lightgray;
  height: 100%;
  width: 100%;

  margin: 0;
  padding: 0;

  text-transform: uppercase;
  font-weight: bold;

  a {
    text-decoration: none;
    padding: 0 1em;
    width: 100%;
    height: 100%;

    align-items: center;
    justify-content: left;
    display: flex;
  }
`

const SCROLL_THRESHOLD = 100

const Navigation = ({ links, shrink }) => {
  return (
    <Container>
      <NavigationContainer className="Navigation" shrink={shrink}>
        <NavigationLogo className="NavigationLogo">
          <Image name="placeholderLogo" style={{ height: "100%" }} />
        </NavigationLogo>
        <NavigationList shrink={shrink}>
          {links.map(item => {
            //
            return (
              <NavigationListItem>
                <a href={`/${item.href}/`}>{item.label}</a>
              </NavigationListItem>
            )
          })}
        </NavigationList>
      </NavigationContainer>
    </Container>
  )
}

export default Navigation
