/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/use-static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { useStaticQuery, graphql } from "gatsby"

import Header from "./header"
import Navigation from "./navigation"
import "./layout.css"
import styled from "styled-components"

import useScrollPositionY from "../hooks/useScrollPosition"

const links = [
  { label: "Story", href: "story" },
  { label: "Products", href: "products" },
  { label: "Team", href: "team" },
  { label: "Other...", href: "other" },
]

const NavigationContainer = styled.div`
  // display: flex;
  // padding: 0 2em;
  // height: ${({ shrink }) => (shrink ? 50 : 100)}px;
  // transition: height 1s, scale 1s;
  // background: orange;
  // opacity: 0.5;
  position: sticky;
  top: -30px;
  z-index: 1000;

  ${({ shrink }) =>
    shrink &&
    `
      header {
        heigh: 0;
      }

    `}
`

const StyledFooter = styled.footer`
  height: 50px;
  width: 100%;
  background: gray;
  color: white;
  padding: 1em 2em;

  margin-top: auto;
`

const SCROLL_THRESHOLD = 80
const OFFSET = 97

const Layout = ({ children, fullWidth }) => {
  const data = useStaticQuery(graphql`
    query SiteTitleQuery {
      site {
        siteMetadata {
          title
        }
      }
    }
  `)

  // VERTICAL PAGE with STICKY NAV on top -> better on mobile
  const [shrink, setShrink] = React.useState(false)

  const [pos, setPos] = React.useState(0) // reference point

  const [dir, setDir] = React.useState(null)

  // if Y position is over 120, make header smaller
  const scrollPositionY2 = useScrollPositionY(500)

  React.useEffect(() => {
    const oldPos = pos // take previous position from state

    if (scrollPositionY2 === 0 && shrink) {
      setShrink(false)
      setPos(scrollPositionY2) // new reference
    }
    if (!shrink && oldPos < scrollPositionY2 - SCROLL_THRESHOLD) {
      setShrink(true)
      setPos(scrollPositionY2) // new reference
    } else if (
      shrink &&
      oldPos > scrollPositionY2 + SCROLL_THRESHOLD + OFFSET
    ) {
      setShrink(false)
      setPos(scrollPositionY2) // new reference
    }
    if (!shrink && oldPos > scrollPositionY2) {
      setPos(scrollPositionY2) // new reference as were moving in same direction
    } else if (shrink && oldPos < scrollPositionY2) {
      setPos(scrollPositionY2) // new reference as were moving in same direction
    }

    // if (shrink && scrollPositionY2 < 80) {
    //   setShrink(false)
    // }
    // if (!shrink && scrollPositionY2 > 130) {
    //   setShrink(true)
    // }
  }, [scrollPositionY2])

  return (
    <>
      <div
        style={{
          position: "sticky",
          top: 0,
          zIndex: 10000,
          transform: shrink ? `translate3d(0, -${OFFSET - 5}px, 0)` : "none",
          transition: "transform .15s",
        }}
      >
        <NavigationContainer
          style={{
            // position: "relative",
            width: "100%",
            // position: "sticky",
            // top: 0,
            // transform: shrink ? `translate3d(0, -${OFFSET - 5}px, 0)` : "none",
            // transition: "transform .15s",
          }}
          shrink={shrink}
        >
          <Header siteTitle={data.site.siteMetadata.title} shrink={shrink} />

          <Navigation links={links} shrink={shrink} />
        </NavigationContainer>
        {/* {shrink ? "SHRINKED" : "NOT SHRINK"} -- Ypos: {scrollPositionY2} */}
      </div>
      <div style={{ display: "flex", flexDirection: "column" }}>
        {/* <Navigation links={links} /> */}
        <div
          style={{
            margin: fullWidth ? "0" : `0 auto`,
            maxWidth: fullWidth ? "100%" : 960,
            padding: fullWidth ? "0" : `0 1.0875rem 1.45rem`,
            marginTop: fullWidth ? "0" : "2em",
          }}
        >
          <main>{children}</main>
        </div>
      </div>
      <StyledFooter>© {new Date().getFullYear()} COMPANY_NAME</StyledFooter>
    </>
  )
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
